import React, { useRef } from "react";
import StarRatings from "react-star-ratings";
import { Link } from "react-router-dom";
import { handleaddtoCart } from "../actions/cartAction";
import { connect } from "react-redux";
import { toast } from "react-toastify";
import { toastStyler } from "../commonEquipment";

function EachProduct(props) {
  // console.log("each product:", props.content);
  // Getting the data from Products.js as Props
  const { productName, image, price, rating, discount, _id } = props.content;
  const WishlistIconChange = useRef(null);
  const CartIconChange = useRef(null);
  //  Using Refs to change image while hovering just like DOM
  const WonHover = () => {
    WishlistIconChange.current.src =
      "data:image/jpeg;base64,";
  };
  const WonRemove = () => {
    WishlistIconChange.current.src =
      "data:image/png;base64,";
  };
  // console.log(props);
  return (
    <div>
      <div className="each-product">
        {/* Wishlist and Car Icons */}
        <p id="wishlistaction">
          <img
            id="beforeadding"
            ref={WishlistIconChange}
            style={{ height: 30, width: 30 }}
            src="data:image/png;base64,"
            onMouseOver={() => {
              WonHover();
            }}
            onMouseOut={() => {
              WonRemove();
            }}
            alt="wishlist"
          />
          <img
            ref={CartIconChange}
            style={{ height: 30, width: 30 }}
            src="data:image/png;base64,"
            onClick={() => {
              props.dispatch(handleaddtoCart(_id));
              toast.success("Your Item Added to Cart", toastStyler);
            }}
            alt="cart"
          />
        </p>
        <div>
          {/* Product Image */}
          <Link to={`/products/${_id}`}>
            <div className="img-size">
              <img
                className="productspage-image"
                src={image[0]}
                alt={productName}
              />
            </div>
          </Link>
        </div>
        {/* Product Details are Here */}
        <div className="product-details-mini">
          {/* <h3>{productName}</h3> */}
          <p>{productName}</p>
          {/* <p> */}
          <StarRatings
            rating={rating.rate}
            starDimension="20px"
            starSpacing="2px"
            starRatedColor="#FF9529"
          />
          ({rating.count} ratings)
          {/* </p> */}
          <p>
            <b>{"₹" + Math.ceil(((100 - discount) / 100) * price)}</b>
            <strike> {"₹" + price}</strike>
            <span className="discount_percentage">
              {" Save " +
                " ₹" +
                Math.ceil(price - ((100 - discount) / 100) * price) +
                ("(" + discount + "% )")}
            </span>
          </p>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    isLoggedIn: state.user.isLoggedIn,
  };
};

export default connect(mapStateToProps)(EachProduct);
